import { createApp } from 'vue'
import App from './App.vue'
import { createStore } from 'vuex'
import { createRouter, createWebHistory } from 'vue-router'

import ConneIon from './components/conne-ion.vue'
import NewUser from './components/new-user.vue'
// import ViDe  from './components/vi-de.vue'
import CatePaymt from './components/cate-paymt.vue'
import MeNu from './components/me-nu.vue'
import Home from './components/home-options.vue'
// import AfriCan from './components/payments/afri-can.vue'
import BanMisr from './components/payments/interna_banks/ban-misr.vue'

const store = createStore({
    state(){
        return {
            counter : 0,
            username : 'aaa',
            password : 'bbb',
            user : 'null',
            isLoggedIn : false,
            isAdmin : false,
            showParent: true,
            actualRoute: 'home',
            categorie: true,
            action : '',
            contWidth : 120,
            contHeight : 0,

        }
    },
    mutations:{
        setCSRFToken(state, csrftoken){
            state.csrftoken = csrftoken
        }
    }
})

const router = createRouter({
    history: createWebHistory(),
    routes : [
        { 
            path : '/', 
            component: ()=> import('./components/ho-me.vue')
        },
        { path: '/connexion', name:'injira' ,component: ConneIon},
        { path: '/signup', component: NewUser},
        { 
            path: '/home', 
            component: ()=> import('./components/home-options.vue'),
            name: 'home',
        },
        {
            path: '/menu',
            component: ()=> import('./components/me-nu.vue'),
            name: 'menu',
        },
        { path: '/currencies', component: CatePaymt , name:'categorie',},
        {   path : '/african',
            component: ()=> import('./components/payments/african_banks/ind-ex.vue'),
            name : 'africano'

        },
        {
            path : '/misr',
            component: BanMisr,
            name : 'misr',
        },
        {
            path : '/international', 
            // Using Lazy loading on this component to save ressources
            // component: ()=> import('./components/inter-national.vue'),
            component : ()=> import('./components/payments/interna_banks/ind-ex.vue'),
            name : 'interna'
        },
        {
            path : '/mobmoney',
            component: ()=> import('./components/payments/mobile_money/ind-ex.vue'),
            name : 'mobilemoney',
        },
        {
            path : '/lumicash',
            component: ()=> import('./components/payments/mobile_money/transf-lumi.vue'),
            name :'lumicash',
        },
        {
            path : '/ecocash',
            component: ()=> import('./components/payments/mobile_money/transf-eco.vue')
        }

    ]
    
})
const app = createApp(App)

app.component('me-nu',MeNu)
app.component('ho-me',Home)
//registering the for 'for-admin' using Lazy loading to save initial load
app.component('for-admin', () => import('./components/for-admin.vue'))
app.use(store)
app.use(router)

app.mount('#app')
