<template>
     <div>
      <form>
        <label for="username">Username</label>
        <input placeholder="Your username" v-model="username"/>
        <p v-if="userExist === 201">This username is already taken
        </p>
        <br/>
        <label for="password">Password</label>
        <input type="password" placeholder="Your Password" v-model="password" 
        v-on:click="chePa"/>
        <br/>
        <label for="email">Email</label>
        <input type="email" placeholder="Your Email" v-model="email">
        <br/>
        <label for="phone">Phone Number</label>
        <input type="number" placeholder="Phone number" v-model="phone">
        <br/>
        <input type="button" value="Add New user" @click="addU"/>
        <p class="para">State : {{ this.$store.state.counter }}</p>
        <div class="para state" 
        :class="canSubmit ? 'cansub' : 'state'"></div>
      </form>
    </div>
</template>

<script>
export default {
    
    data(){
        return {
            userExist: 0,
            canSubmit: false,
            username : '',
            password : '',
            phone :'',
            email :'',
        }
    },
    methods:{
        chePa(){
            if (this.username.length > 2){
                console.log("Username element lost focus... ", this.username)
                // Now ask server for the availabilty of the username
                // from the server

                fetch("http://127.0.0.1:8000/jov/api/userquery/isUserNew/",{
                    method: 'POST',
                    headers:{
                        'Content-Type':'application/json'
                    },
                    body: JSON.stringify({
                        username : this.username
                    })
                }).then((response)=>{
                    if (!response.ok){
                        this.userExist = response.status
                        throw new Error("Answer not ready")
                    }
                    this.userExist = response.status
                    return response.json()
                }).then((data)=>{
                    console.log("The answer is: ", data)
                    if (this.userExist == 200){
                        this.canSubmit = true
                    } else if (this.userExist == 201){
                        this.canSubmit = false
                    }
                })
            }
        },
        addU(){
            const postData = {
                'username': this.username,
                'password': this.password,
            }
            if (this.canSubmit && this.password.length > 3){
                fetch('http://127.0.0.1:8000/jov/api/user/', {
                    method: 'POST',
                    headers : {
                        'Content-Type' : 'application/json',
                    },
                    body : JSON.stringify(postData)
                }).then((response)=>{
                    if (!response.ok){
                        throw new Error("Something went wrong")
                    }
                    return response.json()
                }).then((data)=>{
                    console.log("This is the data: ", data)
                })
            } else {
                this.canSubmit = false
            }
            if (this.password.length <= 5){
                console.log("You submitted with :", this.password.length, )
            }
            
        }

    }
}
</script>