<template>
    
    <div class="same">
        <div class="welc welcimage">
            <p>Welcome To Lde with width: {{ this.$store.state.contWidth }} px</p>
        </div>
        <div class="menu">
            <div class="circle"
                v-bind:class="option1 ? 'circleaux' : 'circle'"
                v-on:click="selC('A')">
                <!-- <p>Conne</p> -->
                <router-link class="tex" to="/connexion">Se Connecter</router-link>
            </div>
            <div class="circle"
                v-bind:class="option2 ? 'circleaux' : 'circle'"
                v-on:click="selC('B')">
                <!-- <p>Conne</p> -->
                <router-link class="tex" to="/signup">S'inscrire</router-link>
            </div>
        </div>
    </div>
    
    <div>
    </div>
</template>

<script>
export default {
    data(){
        return {
            option1 : false,
            option2 : false,
            option3 : false,
            value : false
        }
    },
    methods:{
        selC(option){
            if (option == 'A'){
                this.option1 = !this.option1
                this.option2 = this.option3 = false
                console.log("You have clicked option:", option)
            } else if (option == 'B'){
                this.option2 = !this.option2
                this.option1 = this.option3 = false
                this.$store.state.counter++
            } else {
                this.option3 = !this.option3
                this.option1 = this.option2 = false
            }
            this.$emit('options',{
                option1 : this.option1,
                option2 : this.option2,
                option3 : this.option3,
            })
            
        }
    }
}
</script>

