<template>
    <div class="same">
      <div class="centered">
            <p>Please Connect</p>
      </div>
        <form>
            <div class="user">
              <input class="fields" placeholder="Your username" v-model="username"/> <br>
              <input class="fields" type="password" placeholder="Your Password" v-model="password"/> <br>
            </div>
            <br>
            <div class="centered">
              <input type="button" value="Connexion" @click="connE"/>
            </div>
            
        </form>
        <!-- <p> {{ $store.getters.finalCounter}} </p> -->
        <!-- <p> {{ $store.state.counter}} </p> -->
        <!-- <p> {{ $store.getters.standCounter}} </p> -->
        <!-- <button @click="add1">Add 1</button> -->
    </div>
</template>

<script>

export default {

    name: 'conne-ion',

    data(){
        return {
            username :'',
            password : '',
            jove: "Thierry Jovin",
        }
    },
    created(){
      console.log("Now you put in place the component")
    },
    updated(){
      // const csrftoken = this.getCookie('csrftoken')
      // this.$store.state.n_csrftoken = csrftoken
      // console.log("You have a new updated Token")

    },
    beforeUnmount(){
      console.log("You are about to unmount")
    },
    methods : {
        checkAdmin(){
            // This is to check whether the access token is still valid
            try {
              fetch('http://127.0.0.1:8000/jov/api/userquery/isLoggedin/',{
                method : 'POST',
                headers: {
                    'Content-Type' : 'application/json',
                    'Authorization' : 'Bearer '+ this.$store.state.user.access
                    // 'Cookie' : this.$store.state.token,
                },
            }).then((response)=>{
              if (response.ok){
                this.$store.state.isLoggedIn = true
              } else {
                this.$store.state.isLoggedIn = false
              }
              return response.json()
            }).then((data)=>{
                this.$store.state.isAdmin = data.is_Admin
                console.log("The data received: ", data)

            }).catch((error)=>{
              console.log("THis is the error: ", error.message)
            })
            } catch(error){
              console.log("THE ERROR WE CAPTURED Is :", error.message)
            } finally {
              console.log("CHECK Finished")
            }
        },
        add1(){
            this.$store.dispatch('addT', {
                jove : 3
            })
        },
        
        connE(){
            this.$store.state.username = this.username
            this.$store.state.password = this.password
            console.log("You want to connect")
            fetch("http://127.0.0.1:8000/jov/api/token/", {
                method:'POST',
                headers: {
                    'Content-Type':'application/json',
                },
                body: JSON.stringify({
                    username : this.username,
                    password : this.password,
                }),
            }).then((response)=>{
                if (!response.ok){
                  this.$store.state.isLoggedIn = false
                  return new Error("Server not reached")
                //   throw new Error('Network response was not ok');
                } else{
                  this.$store.state.isLoggedIn = true
                  return response.json()
            }
            }).then((data)=>{
                console.log("Now th data is :", data)
                console.log("The Access is: ", data.access)
                this.$store.state.user = data
                this.$store.state.isLoggedIn = true
                localStorage.setItem('token', data.access)
                localStorage.setItem('refresh', data.refresh)
                this.checkAdmin()

                this.$router.push('/')
            }).catch((error)=>{
                this.$store.state.isLoggedIn = false
                console.log("Here is the error : ", error.message)
            });
        },
    },
}
</script>

